export function serialize(obj: any) {
    const str = [];
    for (const key in obj) {
        if (Array.isArray(obj[key])) {
            for (let i = 0; i < obj[key].length; i++) {
                str.push(key + '=' + obj[key][i]);
            }
        } else {
            str.push(key + '=' + obj[key]);
        }
    }
    return str.join('&');
}



